/* eslint-disable */
<template>
  <div class="stat">
    <div class="text"><p><i class="fas fa-chart-bar"></i> {{ $t("stat.mes") }}</p></div>
    <div class="chart">
      <zingchart :data="chartData"></zingchart>
    </div>
    <br>
    <div class="grid-container">
      <div class="duration">
        <div class="bol">
          <p class="ti">{{distance}}</p>
        </div>
        <div>
          <p class="tf"><i class="fas fa-fire"></i> {{ $t("stat.totaldistance") }}</p>
        </div>
      </div>
      <div class="best">
        <div class="bol">
          <p class="ti">{{best}}</p>
        </div>
        <div>
          <p class="tf"><i class="fas fa-arrow-alt-circle-up"></i> {{ $t("stat.best") }}</p>
        </div>
        <br>
      </div>
      <div class="average">
        <div class="bol">
          <p class="ti">{{average}}</p>
        </div>
        <div>
          <p class="tf"><i class="fas fa-balance-scale"></i> {{ $t("stat.average") }}</p>
        </div>
        <br>
      </div>
    </div>
    <br>
    <div class="navstat">
      <router-link to="/distance" class="link"> {{ $t("content.distance") }}</router-link>
      <router-link to="/calories" class="link action "> {{ $t("content.kcal") }}</router-link>
      <router-link to="/duration" class="link ">{{ $t("content.duration") }}</router-link>
    </div>
  </div>
</template>

<script>

export default {


  data () {
    return {
      
      distance:localStorage.getItem('distance') || 0,
      best:0,   
      average:0,
      chartData: {
                type: 'bar',
                plot:{
                  'background-color':'#41AA64',
                  animation: {
                    effect: "ANIMATION_FADE_IN"
                  }
                },
                height: "100%",
                width:"100%",
                x: "0%",
                y: "0%",
                series: [{
                    values: JSON.parse(this.$localStorage.get('bestsd')) || 0
                }]
      }
    };
  },
  methods: {
    bestescore() {
      var score = JSON.parse(this.$localStorage.get("bestsd")) || 0;
      if(score == 0){
        this.best = score;
      }else{
        if (score){
          this.score = score;
        }
        var copie = [];
        for (let i=0; i< score.length; ++i){
          let b = parseFloat(score[i]);
          copie.push(b);  
        }
        var best1= Math.max(...copie);
        this.best = best1;
      };

    },
    averagelist() {
      var score = JSON.parse(this.$localStorage.get('bestsd')) || 0
      if(score == 0){
        this.average = score
      }else{

        if (score){
          this.score = score
        }
        var sum = 0
        for (let i=0; i< score.length; ++i){
          let b = parseFloat(score[i])
          sum += b 
        }
        var avg = sum/score.length
        this.average = avg.toFixed(2)
      }

    } 
  },
    created() {
    this.bestescore();
    this.averagelist();
  }

};

</script>

<style scoped>

.stat{ 
    text-align: center;
}
.grid-container {
  display: grid;
  border: 2px solid rgb(31, 31, 31);
  border-radius: 6px;
  width: 65%;
  margin: auto;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "duration duration"
    "best average";
}
.duration {
  grid-area: duration;
}
.Best {
  grid-area: Best;
}
.average {
  grid-area: average;
}

.tf{
  font-size: 1.2em;
}


.ti{
  font-weight: bold;
  font-size:30px;
}

.navstat{
  padding: 30px;
  width:65%;
  margin:auto;

}

.navstat a{
  padding: 20px;
  text-decoration: none;
  color: rgb(29, 29, 29);
  font-weight: bold;
  cursor:pointer;
}

 .link:hover{
  color:#41AA64;
  transition: 0.5s;
}

.action{
  border-right: 2px solid rgb(31, 31, 31);
  border-left: 2px solid rgb(31, 31, 31);
}

.chart{
  margin: auto;
  text-align: center;
  width: 65%;
  border: 2px solid  rgb(43, 43, 43);
}

.text p{
  font-size: 25px;
}


</style>